/******************************************************************

Stylesheet: Login Stylesheet

This stylesheet is loaded is only on the login page. This way you can style
the login page. It won't affect any other page, admin or front-end.

Make sure functions/login.php is activated in your functions.php file.

This stylesheet is turned off by default.

For more info, check out the codex:
http://codex.wordpress.org/Creating_Admin_Themes

******************************************************************/
body.login {
  background: #eff7fb;
  color: #1C3A49;
}

.login h1 a {
  background: url(../images/login-logo.png) no-repeat top center;
  background-size: contain;
  width: 326px;
  text-indent: -9999px;
  overflow: hidden;
  padding-bottom: 15px;
  display: block;
}

.login input[type=text], .login input.password-input {
  background-color: white;
  border: 0.0625em solid #c4dae3;
  border-radius: 0.125em;
}
.login input[type=text]:focus, .login input.password-input:focus {
  border-color: #1C3A49;
}

.login .message, .login .notice {
  border-left-color: #19B69C;
  box-shadow: 0 1px 1px 1px rgba(0, 0, 0, 0.1);
}

.wp-core-ui .button, .wp-core-ui .button-secondary {
  color: #586C75;
}

.wp-core-ui .button:focus {
  color: #586C75;
}

#wp-submit {
  background-color: #008771;
  color: white;
  border: 0.0625em solid #008771;
  border-radius: 0.125em;
  font-size: 15px;
  font-weight: 450;
  letter-spacing: 0.046875em;
  text-transform: uppercase;
  padding: 10px 16px 8px;
  vertical-align: middle;
  line-height: 1;
}